import {
  Component,
  ViewChild,
  ElementRef,
  OnInit,
  OnDestroy,
} from '@angular/core';

interface HTMLPositon {
  top: string;
  left: string;
}

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.less'],
})
export class AppComponent implements OnInit, OnDestroy {
  title = 'simple-video-player';
  @ViewChild('videoPlayer', { static: true }) player: ElementRef<
    HTMLVideoElement
  >;
  adPosition?: HTMLPositon;
  buttonPostion?: HTMLPositon;
  showOverlay = false;
  timerHandler?: number;

  ngOnInit() {
    const player = this.player.nativeElement;
    const poll = () => {
      console.log(this.player.nativeElement.currentTime);
      if (player.currentTime >= 44) {
        this.showOverlay = true;
        const rect = player.getBoundingClientRect();
        const absRect = (prop: 'top' | 'bottom' | 'left' | 'right') =>
          Math.abs(rect[prop]);
        this.adPosition = {
          top: `${rect.bottom + player.scrollTop - 100}px`,
          left: `${absRect('left') + player.scrollLeft}px`,
        };
        this.buttonPostion = {
          top: `${rect.top + player.scrollTop + 25}px`,
          left: `${absRect('right') + player.scrollLeft - 100}px`,
        };
      }
    };
    this.timerHandler = window.setInterval(poll, 10);
  }
  onHideAd() {
    window.clearInterval(this.timerHandler);
    this.showOverlay = false;
  }
  ngOnDestroy() {
    window.clearInterval(this.timerHandler);
  }
}
